.login-container {
  max-width: 400px;
  margin: 40px auto;
  padding: 24px;
  background: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0 0 10px #ccc;
}

.login-container h2 {
  text-align: center;
  margin-bottom: 16px;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.login-form input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.login-form button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.login-form button:hover {
  background-color: #0056b3;
}

.mensaje-login {
  margin-top: 16px;
  padding: 12px;
  border-radius: 6px;
  color: white;
}

.mensaje-login.exito {
  background-color: green;
}

.mensaje-login.error {
  background-color: crimson;
}
