.product-detail-container {
  max-width: 1000px;
  margin: 40px auto;
  padding: 20px;
}

.product-detail-card {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  align-items: flex-start;
  background: #fff;
  padding: 24px;
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
}

.product-detail-image {
  max-width: 300px;
  width: 100%;
  border-radius: 8px;
  object-fit: cover;
}

.product-detail-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.product-detail-price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #28a745;
}

.product-detail-stock {
  color: #333;
  font-size: 1rem;
}

.product-detail-description {
  color: #555;
  line-height: 1.6;
}

.product-detail-button {
  padding: 12px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.product-detail-button:hover {
  background-color: #0056b3;
}

.product-loading {
  text-align: center;
  margin-top: 60px;
  font-size: 1.2rem;
}
.product-error {
  color: white;
  background-color: crimson;
  padding: 16px;
  border-radius: 6px;
  max-width: 600px;
  margin: 30px auto;
  text-align: center;
}