.carrito-main {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.08);
}

.carrito-vacio {
  text-align: center;
  font-size: 1.2rem;
  color: #555;
}

.carrito-lista {
  list-style: none;
  padding: 0;
  margin: 0;
}

.carrito-item {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 16px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 12px;
}

.carrito-img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 8px;
}

.carrito-info {
  flex: 1;
}

.carrito-total {
  margin-top: 20px;
  font-size: 1.3rem;
  font-weight: bold;
}

.carrito-botones {
  display: flex;
  gap: 12px;
  margin-top: 24px;
}

.btn-confirmar {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
}

.btn-confirmar:hover {
  background-color: #218838;
}

.btn-vaciar {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
}

.btn-vaciar:hover {
  background-color: #bd2130;
}
.carrito-item h3 {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
}