.registro-container {
  max-width: 500px;
  margin: 40px auto;
  padding: 24px;
  background-color: #fefefe;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    font-family: Arial, sans-serif;
    color: #333;
    border: 1px solid #ccc;
    display: flex;
}

.registro-container h2 {
  text-align: center;
  margin-bottom: 16px;
}

.registro-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.registro-form input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.registro-form button {
  padding: 12px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.2s ease;
}

.registro-form button:hover {
  background-color: #218838;
}

.mensaje-registro {
  margin-top: 16px;
  padding: 12px;
  border-radius: 6px;
  color: white;
  text-align: center;
}

.mensaje-registro.exito {
  background-color: green;
}

.mensaje-registro.error {
  background-color: crimson;
}
.registro-form label {
  font-weight: bold;
  margin-bottom: 4px;
}